.odc-multiple-key-selector {
  padding-top: var(--pf-global--spacer--md);
}

.odc-multiple-key-selector__item {
  position: relative;
  padding-top: var(--pf-global--spacer--xs);
}

.odc-multiple-key-selector__deleteButton {
  cursor: pointer;
  position: absolute;
  right: -22px;
  top: 25px;
}

.odc-multiple-key-selector__deleteButton.--disabled {
  pointer-events: none;
  color: var(--pf-global--disabled-color--200);
}

